@import url("https://fonts.googleapis.com/css?family=Alegreya+Sans|Alegreya+Sans+SC");

body {
  font-family: "Alegreya Sans", sans-serif;
}

.wrapper {
  background: #c2e59c; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #6441a5,
    rgb(68, 27, 27)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to right,
    #6441a5,
    rgb(68, 27, 27)
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0, 0, 0, 0.75);
  margin: 0 auto;
  border-radius: 15px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.24);
}

.title-container {
  height: 90vh;
  width: 50vh;
  background-image: url("https://hdqwalls.com/download/minimalism-sky-clouds-2932x2932.jpg");
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__title {
  font-size: 40px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: "Alegreya Sans SC", sans-serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Alegreya Sans", sans-serif;
}

.form-container {
  background-color: #2c3e50;
  height: 90vh;
  width: 40vh;
  padding-top: 100px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  display: inline;
  text-align: center;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  font-style: italic;
}

input[type="text"] {
  outline: none;
  width: 80%;
  font-size: 1.2em;
}

input:-webkit-autofill {
  box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 5px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
  -webkit-box-shadow: 10px 9px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 10px 9px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 10px 9px 5px 0px rgba(0, 0, 0, 0.75);
  transition: all ease 0.3s;
}

button:active {
  outline: none;
  transform: translateY(5px);
  -webkit-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.75);
}

button:hover {
  transform: translateY(-5px);
}

.weather__info {
  font-size: 20px;
  letter-spacing: 2px;
  display: inline;
}

.weather__key {
  font-size: 0.9em;
  color: #f16051;
  /* border-bottom: solid 2px rgba(255,255,255,0.06); */
  padding: 5px 0 5px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}
.weather__key:first-child {
  margin-top: 72px;
}

.weather__value {
  color: #fff;
  font-weight: 200;
  text-transform: capitalize;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
  margin-top: 30px;
}

.row {
  flex-wrap: nowrap !important;
}

@media (min-width: 1200px) {
  .container {
    width: auto;
  }
}
@media (min-width: 768px) {
  .container {
    width: auto;
  }
}
@media (min-width: 992px) {
  .container {
    width: auto;
  }
}
@media (min-width: 200px) {
  .container {
    width: auto;
  }
}
